<template>
<div class="boxWithSlides">
  <!-- Slideshow container -->
  <div class="slideshow-container">

    <!-- Full-width images with number and caption text -->
    <div class="mySlides fade">
      <!--div class="numbertext">1 / 3</div-->
      <img src="../assets/slideShow/slide1.jpg">
      <div class="captionText">Защита</div>
    </div>

    <div class="mySlides fade">
      <!--div class="numbertext">2 / 3</div-->
      <img src="../assets/slideShow/slide2.jpg">
      <div class="captionText">Послушание</div>
    </div>

    <div class="mySlides fade">
      <!--div class="numbertext">3 / 3</div-->
      <img src="../assets/slideShow/slide3.jpg">
      <div class="captionText">Социализация собаки</div>
    </div>

    <div class="mySlides fade">
      <!--div class="numbertext">3 / 3</div-->
      <img src="../assets/slideShow/slide4.jpg">
      <div class="captionText">Груминг</div>
    </div>

    <!-- Next and previous buttons -->
    <a class="prev" v-on:click="plusSlides(-1); disableAutoplay()">&#10094;</a>
    <a class="next" v-on:click="plusSlides(1); disableAutoplay()">&#10095;</a>
  </div>
  <br>

  <!-- The dots/circles -->
  <div style="text-align:center">
    <span class="dot" v-on:click="currentSlide(1)"></span>
    <span class="dot" v-on:click="currentSlide(2)"></span>
    <span class="dot" v-on:click="currentSlide(3)"></span>
    <span class="dot" v-on:click="currentSlide(4)"></span>
  </div>
</div>
</template>

<script>
import store from '../store'

export default {
  name: 'boxWithSlides',
  props: {
    msg: String
  },
  mounted: function () {
    // console.log('Mounted.')
    this.showSlides(1)
  },
  methods: {
    showSlides: function (slideIndex) {
      // set current slide
      store.state.currentSlide = slideIndex
      let i
      let slides = document.getElementsByClassName('mySlides')
      // set number of slides to the store
      store.state.numberOfSlides = slides.length
      let dots = document.getElementsByClassName('dot')
      // check range
      if (slideIndex > slides.length) { slideIndex = 1 }
      // check if zero
      if (slideIndex < 1) { slideIndex = slides.length }
      // set all slides display to 'none'
      for (i = 0; i < slides.length; i++) {
        slides[i].style.display = 'none'
      }
      // clear all active dots
      for (i = 0; i < dots.length; i++) {
        dots[i].classList.remove('active')
      }
      slides[slideIndex - 1].style.display = 'block'
      dots[slideIndex - 1].classList.add('active')
      /*
      if (store.state.autoPlay) {
        // console.log(`autoplay enabled`)
        store.state.slideShowPlay = setTimeout(() => { this.plusSlides(1) }, 5000)
      }
      */
    },
    plusSlides: function (increment) {
      // set current slide
      store.state.currentSlide = store.state.currentSlide + increment
      if (store.state.currentSlide > store.state.numberOfSlides) {
        // set to first one and show it
        store.state.currentSlide = 1
        this.showSlides(store.state.currentSlide)
      } else if (store.state.currentSlide <= 0) {
        // loop to the last slide
        store.state.currentSlide = store.state.numberOfSlides
        this.showSlides(store.state.currentSlide)
      } else {
        // else just show the slide already
        this.showSlides(store.state.currentSlide)
      }
    },
    currentSlide: function (slideIndex) {
      this.showSlides(slideIndex)
    },
    disableAutoplay: function () {
      if (store.state.autoPlay) {
        clearTimeout(store.state.slideShowPlay)
        store.state.autoPlay = false
        this.showSlides(store.state.currentSlide - 1)
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
$color-white: hsl(0, 0%, 100%);
$color-red: hsl(0, 100%, 41%);
$color-black: hsl(0, 0%, 0%);
$color-lightGray: hsl(0, 0%, 73%);
$color-grayShadow: hsla(0, 0%, 80%, 0.75);
$color-darkGray: hsl(0, 0%, 33%);

.boxWithSlides {
  width: 100%;
  display: flex;
  flex-direction: column;
}

/* Slideshow container */
.slideshow-container {
  // width: 500px;
  position: relative;
  margin: auto;
  // border: 1px solid black;
  // background-color: #717171;
  // box-shadow: 0px 0px 5px 1px $color-lightGray;
}

/* Hide the images by default */
.mySlides {
    display: none;
}

.mySlides img {
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
}

/* Next & previous buttons */
.prev, .next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  margin-top: -22px;
  padding: 16px;
  color: $color-lightGray;
  font-weight: bold;
  font-size: 18px;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
}

/* Position the "next button" to the right */
.next {
  right: 0;
  border-radius: 3px 0 0 3px;
}

/* On hover, add a black background color with a little bit see-through */
.prev:hover, .next:hover {
  background-color: rgba(0,0,0,0.5);
  color: $color-white;
}

/* Caption text */
.captionText {
  color: #f2f2f2;
  font-size: 1.5em;
  padding: 8px 12px;
  position: absolute;
  bottom: 18px;
  right: 0px;
  // width: 100%;
  text-align: center;
  background-color: hsla(0, 0%, 0%, 0.5);
}

/* The dots/bullets/indicators */
.dot {
  cursor: pointer;
  height: .6em;
  width: .6em;
  margin: 0em .4em 0em .4em;
  background-color: $color-lightGray;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
}
.active {
  background-color: $color-red;
}
.dot:hover {
  background-color: $color-darkGray;
}

/* Fading animation */
.fade {
  -webkit-animation-name: fade;
  -webkit-animation-duration: 2s;
  animation-name: fade;
  animation-duration: 2s;
}

@-webkit-keyframes fade {
  from {opacity: .2}
  to {opacity: 1}
}

@keyframes fade {
  from {opacity: .2}
  to {opacity: 1}
}

</style>
